import React, { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useSetRecoilState } from "recoil";
import { IsUserLoggedIn, UserInformation } from "../global/ProjectCommon";
import { useToast } from "@chakra-ui/react";

export default function Social() {
    const toast = useToast();
    const param = useParams();
    const navigate = useNavigate();
    const setIsUserLoggedIn = useSetRecoilState(IsUserLoggedIn);
    const setUserInformation = useSetRecoilState(UserInformation);

    useEffect(() => {
        if (
            param &&
            param.uid &&
            param.email &&
            param.nickname &&
            param.accessToken
        ) {
            toast({
                status: "success",
                title: " ",
                description: ` ${param.nickname}`,
            });
            setUserInformation({
                uid: Number(param.uid),
                email: param.email,
                nickname: param.nickname,
                accessToken: param.accessToken,
            });
            setIsUserLoggedIn(true);
            navigate("/main");
        } else {
            toast({
                status: "error",
                title: "  ",
            });
            navigate("/");
        }
    }, []);

    return <div>Social</div>;
}
