import {
    AbsoluteCenter,
    Box,
    Button,
    Center,
    Divider,
    FormControl,
    FormLabel,
    HStack,
    Image,
    Input,
    Modal,
    ModalBody,
    ModalCloseButton,
    ModalContent,
    ModalHeader,
    ModalOverlay,
    Text,
    VStack,
    useToast,
} from "@chakra-ui/react";
import { IModalProps } from "./StartModal";
import { FieldValues, useForm } from "react-hook-form";
import { useMutation } from "react-query";
import { signUpWithEmail } from "../../Api";
import { ISignUpResponse } from "../../ProjectTypes";
import { onKakaoClick, onNaverClick } from "../../utils/logins/SocialLogin";
import SocialButton from "../../utils/logins/SocialButton";

export default function RegisterModal({ isOpen, onClose }: IModalProps) {
    const {
        reset,
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();
    const toast = useToast();

    const kakaoLogo = require("../../resource/_.png");
    const naverLogo = require("../../resource/_.png");

    const mutation = useMutation(signUpWithEmail, {
        onMutate: () => {
            console.log("signup with email mutation start");
        },
        onSuccess: (result: ISignUpResponse) => {
            // console.log("mutate success");
            // console.log(result);

            toast({
                status: "success",
                title: " ",
                description: "   !",
            });
            CloseModal();
        },
        onError: (result: any) => {
            // console.log("mutate error");
            // console.log(result);
            // console.log(result.response.data.message);
            toast({
                status: "error",
                title: " ",
                description: result.response.data.message,
            });
            CloseModal();
        },
    });

    function CloseModal() {
        reset();
        onClose();
    }

    function onSubmit(data: FieldValues) {
        const email = data.email;
        const password = data.password.toString();
        const nickname = data.nickname;

        if (password.length < 6) {
            toast({
                status: "warning",
                title: "  6 ",
            });
            return;
        }

        mutation.mutate({ email, password, nickname });
    }

    return (
        <Modal isOpen={isOpen} onClose={CloseModal} isCentered size="xl">
            <ModalOverlay />
            <ModalContent bgColor="black">
                <ModalHeader></ModalHeader>
                <ModalCloseButton />
                <ModalBody p="35px">
                    <VStack as="form" onSubmit={handleSubmit(onSubmit)}>
                        <VStack spacing="20px" w="100%">
                            <Input
                                placeholder=""
                                w="100%"
                                type="email"
                                border="1px solid whitesmoke"
                                color="whitesmoke"
                                fontSize="15px"
                                _placeholder={{
                                    color: "whitesmoke",
                                    fontsize: "15px",
                                }}
                                {...register("email", {
                                    required: "",
                                })}
                                isInvalid={Boolean(errors.email?.message)}
                            />
                            <Input
                                placeholder=""
                                w="100%"
                                type="password"
                                border="1px solid whitesmoke"
                                color="whitesmoke"
                                fontSize="15px"
                                _placeholder={{
                                    color: "whitesmoke",
                                    fontsize: "15px",
                                }}
                                {...register("password", {
                                    required: "",
                                })}
                                isInvalid={Boolean(errors.password?.message)}
                            />
                            <Input
                                placeholder=""
                                w="100%"
                                type="text"
                                border="1px solid whitesmoke"
                                color="whitesmoke"
                                fontSize="15px"
                                _placeholder={{
                                    color: "whitesmoke",
                                    fontsize: "15px",
                                }}
                                {...register("nickname", {
                                    required: "",
                                })}
                                isInvalid={Boolean(errors.nickname?.message)}
                            />
                        </VStack>
                        <HStack w="100%" justifyContent="center" mt="20px">
                            <Button
                                w="100%"
                                h="40px"
                                bgColor="purple.300"
                                type="submit"
                            >
                                
                            </Button>
                            <Button
                                w="100%"
                                h="40px"
                                bgColor="purple.300"
                                onClick={CloseModal}
                            >
                                
                            </Button>
                        </HStack>
                        <VStack w="100%">
                            <SocialButton
                                width={400}
                                height={40}
                                logoWidth={20}
                                logoHeight={20}
                                logoColor="#000000"
                                bgColor="#FEE500"
                                logoUrl={kakaoLogo}
                                social=""
                            />
                            <SocialButton
                                width={400}
                                height={40}
                                logoWidth={20}
                                logoHeight={20}
                                logoColor="#FFFFFF"
                                bgColor="#03C75A"
                                logoUrl={naverLogo}
                                social=""
                            />
                        </VStack>
                    </VStack>
                </ModalBody>
            </ModalContent>
        </Modal>
    );
}
