import { useNavigate } from "react-router-dom";
import { IModalProps } from "./StartModal";
import {
    Button,
    HStack,
    Input,
    Modal,
    ModalBody,
    ModalCloseButton,
    ModalContent,
    ModalHeader,
    ModalOverlay,
    VStack,
    useToast,
} from "@chakra-ui/react";
import { FieldValues, useForm } from "react-hook-form";
import { useMutation } from "react-query";
import { signInWithEmail } from "../../Api";
import { ISignInResponse } from "../../ProjectTypes";
import { useSetRecoilState } from "recoil";
import { IsUserLoggedIn, UserInformation } from "../../global/ProjectCommon";
import SocialButton from "../../utils/logins/SocialButton";

export default function LoginModal({ isOpen, onClose }: IModalProps) {
    const toast = useToast();
    const navigate = useNavigate();
    const { reset, register, handleSubmit } = useForm();
    const setIsUserLoggedIn = useSetRecoilState(IsUserLoggedIn);
    const setUserInformation = useSetRecoilState(UserInformation);

    const kakaoLogo = require("../../resource/_.png");
    const naverLogo = require("../../resource/_.png");

    const mutation = useMutation(signInWithEmail, {
        onMutate: () => {
            console.log("Sign in with email mutation start");
        },
        onSuccess: (result: ISignInResponse) => {
            console.log("Sign in with email mutation success");
            // console.log(result);

            setUserInformation({
                uid: result.uid,
                email: result.email,
                nickname: result.nickname,
                accessToken: result.accessToken,
            });
            setIsUserLoggedIn(true);

            toast({
                status: "success",
                title: " ",
                description: ` ${result.nickname}`,
            });

            CloseModal();
            navigate("/main");
        },
        onError: (result: any) => {
            console.log("Sign in with email mutation fail");
            // console.log(result);
            toast({
                status: "error",
                title: " ",
                description: "    ",
            });
            CloseModal();
        },
    });

    function CloseModal() {
        reset();
        onClose();
    }

    function onSubmit(data: FieldValues) {
        /**
         * Can not use {email, password}: FieldValues form
         * Due to we have to convert type of password to string
         */
        const email = data.email;
        const password = data.password.toString();

        mutation.mutate({ email, password });
    }

    return (
        <Modal isOpen={isOpen} onClose={CloseModal} isCentered size="xl">
            <ModalOverlay />
            <ModalContent bgColor="black">
                <ModalHeader></ModalHeader>
                <ModalCloseButton />
                <ModalBody p="35px">
                    <VStack
                        spacing="20px"
                        w="100%"
                        as="form"
                        onSubmit={handleSubmit(onSubmit)}
                    >
                        <Input
                            placeholder=""
                            w="100%"
                            type="email"
                            border="1px solid whitesmoke"
                            color="whitesmoke"
                            fontSize="15px"
                            _placeholder={{
                                color: "whitesmoke",
                                fontsize: "15px",
                            }}
                            {...register("email", { required: true })}
                        />
                        <Input
                            placeholder=""
                            w="100%"
                            type="password"
                            border="1px solid whitesmoke"
                            color="whitesmoke"
                            fontSize="15px"
                            _placeholder={{
                                color: "whitesmoke",
                                fontsize: "15px",
                            }}
                            {...register("password", {
                                required: true,
                            })}
                        />
                        <HStack w="100%">
                            <Button w="100%" bgColor="purple.300" type="submit">
                                
                            </Button>
                            <Button
                                w="100%"
                                bgColor="purple.300"
                                onClick={CloseModal}
                            >
                                
                            </Button>
                        </HStack>
                        <VStack w="100%">
                            <SocialButton
                                width={400}
                                height={40}
                                logoWidth={20}
                                logoHeight={20}
                                logoColor="#000000"
                                bgColor="#FEE500"
                                logoUrl={kakaoLogo}
                                social=""
                            />
                            <SocialButton
                                width={400}
                                height={40}
                                logoWidth={20}
                                logoHeight={20}
                                logoColor="#FFFFFF"
                                bgColor="#03C75A"
                                logoUrl={naverLogo}
                                social=""
                            />
                        </VStack>
                    </VStack>
                </ModalBody>
            </ModalContent>
        </Modal>
    );
}
